================= */


/* SEARCH PAGE
------------------ */
.path-site-search{

	//move superfish toggle over because no search icon
	#superfish-main-toggle{
		right: 20px;
	}

	//prevent contextual links interference when logged in
	.views-element-container{
		position: static;
	}

	.page-main{
		position: relative;
	}

	.layout-content{
		padding: 2em $siteMarginMobile 0 !important; 
		.site-search{
			h1{
				@include tab{
					color: $black;
				}
			}
			.search-page-block{
				margin-bottom: $spaceLg;
			}
			h2.results-header{
				width: 100%;
				max-width: $textMax;
				@include auto;
				padding-bottom: 1rem;
				border-bottom: 1px solid $borderRule;
				margin-bottom: $spaceXs;
			}
			.view-content,.view-empty{
				width: 100%;
				max-width: $textMax;
				@include auto;
				margin-bottom: $spaceXl;
			}
		}//site-search
	}//layout content

}//end search page




/* PATTERN LAB ONLY
------------------ */
.pl .search-header a{
	&:hover,&:focus{
		@include transAll(.3s);
	}
}



