.pl .list-counter {
  counter-reset: item-counter;
}

.pl .list-counter > li:before {
  content: counter(item-counter, decimal) ".";
  counter-increment: item-counter;
  margin-right: 2rem;
}

.pl span.code {
  color: #017aa2;
}

.pl p.subtext {
  padding-left: 60px;
  font-size: 90%;
}
